html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 10px;
  height: 100%; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  height: inherit;
  margin: 0;
  font-family: "Verdana", "Arial", sans-serif;
  font-size: 2rem;
  line-height: 1.2;
  font-weight: 400;
  font-style: normal;
  color: #2e2d2d; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0; }

.textarea {
  display: block;
  width: 80%;
  max-width: 1000px;
  min-height: 100px;
  margin: 0 auto;
  padding: 20px; }

.keyboard {
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
  background-color: #0479da;
  text-align: center; }

.keyboard__key {
  width: 40px;
  height: 40px;
  padding: 0;
  margin: 3px;
  border: none;
  background-color: tomato;
  font-size: 1rem;
  text-align: center;
  vertical-align: middle;
  color: white;
  -webkit-transition: background-color 0.2s ease-in-out;
  transition: background-color 0.2s ease-in-out; }
  .keyboard__key--active {
    background-color: navy; }
  .keyboard__key--size-1 {
    width: 7%; }
  .keyboard__key--size-2 {
    width: 8%; }
  .keyboard__key--size-3 {
    width: 11%; }
  .keyboard__key--size-4 {
    width: 13%; }
  .keyboard__key--size-5 {
    width: 15%; }
  .keyboard__key--size-6 {
    width: 16%; }
  .keyboard__key--size-7 {
    width: 31%; }

.info__title,
.info__description {
  max-width: 50%;
  margin-right: auto;
  margin-left: auto;
  font-size: 16px;
  text-align: center; }
